:root {
  --color-primary-blue: hsl(238, 40%, 52%); /*#C5C6EF*/
  --color-primary-red: hsl(358, 79%, 66%);
  --color-primary-blue-gray: hsl(239, 57%, 85%);
  --color-primary-red-pale: hsl(357, 100%, 86%);
  --color-primary-red-very-pale: hsl(357, 100%, 91%);

  --color-neutral-blue-dark: hsl(212, 24%, 26%);
  --color-neutral-blue-gray: hsl(211, 10%, 45%);
  --color-neutral-gray: hsl(223, 19%, 93%);
  --color-neutral-gray-light: hsl(228, 33%, 97%);
  --color-neutral-white: hsl(0, 0%, 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: hsl(212, 24%, 26%);
  color: var(--color-neutral-blue-dark);
  overflow-y: overlay; /* scrollbar overlay */
}
body {
  overflow-x: hidden;
}

/* scrollbar */
::-webkit-scrollbar {
  width: 0.8rem;
}
::-webkit-scrollbar-thumb {
  background: hsl(357, 100%, 91%);
  background: var(--color-primary-red-very-pale);
}
::-webkit-scrollbar-thumb:hover {
  background: hsl(358, 79%, 66%);
  background: var(--color-primary-red);
}

#root {
  width: 100vw;
  min-height: 100vh;
  background-color: hsl(223, 19%, 93%);
  background-color: var(--color-neutral-gray);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5.5rem 1rem;
}

main {
  width: 66.7%;
}

/* media queries */
@media screen and (max-width: 1000px) {
  main {
    width: 80%;
  }
}
@media screen and (max-width: 800px) {
  main {
    width: 100%;
  }
}

@media screen and (max-width: 700px) {
  /* fixed header flex-wrap -> page padding-top adjustment */
  #root {
    padding-top: 8rem;
  }
}

.center-me {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.delete-modal {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  border-radius: 0.5rem;
  margin: 2rem;
  z-index: 2;
  opacity: 1;
  background-color: var(--color-neutral-white);
  padding: 2rem;
}

.modal-btns {
  width: 100%;
  display: flex;
  justify-content: space-between;
  grid-gap: 1rem;
  gap: 1rem;
}
.btn-modal {
  font: inherit;
  flex-grow: 1;
  border: none;
  color: var(--color-neutral-white);
  text-transform: uppercase;
  padding: 1rem;
  border-radius: 0.5rem;
}
.btn-cancel {
  background-color: var(--color-neutral-blue-dark);
}
.btn-confirm {
  background-color: var(--color-primary-red);
}

.avatar {
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  background-size: contain;
  border: 2px solid transparent;
  border-radius: 50%;
}

.active {
  border-color: var(--color-neutral-white);
  box-shadow: 0 0 5px var(--color-neutral-white);
}

.header {
  width: 100vw;
  position: fixed;
  top: 0;
  font: inherit;
  font-weight: 400;
  padding: 0.5rem 1rem 0.5rem;
  color: var(--color-neutral-white);
  background-color: var(--color-primary-blue);
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}
.header-title {
  font-weight: 400;
}
.header-subtitle {
  display: inline-block;
  font-size: 0.9rem;
  margin-left: 1rem;
  text-decoration: none;
  color: var(--color-primary-red-pale);
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}
.header-subtitle:hover {
  color: var(--color-primary-red);
}

.users {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  align-items: center;
}

.user-select-label {
  font-size: 0.8rem;
  color: var(--color-neutral-gray);
}

@media screen and (max-width: 700px) {
  .header {
    flex-direction: column;
    align-items: flex-start;
  }

  .users {
    align-self: flex-end;
  }
}

.vote-widget {
  background-color: var(--color-neutral-gray);
  border-radius: 0.5rem;
  align-self: flex-start;
  display: flex;
  flex-direction: column;
}
.vote-widget > * {
  height: 2rem;
  width: 2rem;
}
.vote-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 1px solid transparent;
  background-size: 33%;
  opacity: 0.5;
}
.vote-btn:hover,
.vote-btn:focus {
  outline: none;
  cursor: pointer;
}
.vote-btn:hover {
  opacity: 1;
}
.vote-count {
  color: var(--color-primary-blue);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
}

@media screen and (max-width: 600px) {
  .vote-widget {
    order: 1;
    flex-direction: row;
  }
}

.user-tag {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.you {
  font-size: 0.8rem;
  padding: 0.1rem 0.25rem;
  color: var(--color-neutral-white);
  background-color: var(--color-primary-blue);
}

.comment-input-block {
  display: flex;
  width: 100%;
  grid-gap: 1rem;
  gap: 1rem;
}
.editing {
  flex-direction: column;
  align-items: flex-end;
}

.comment-input-content {
  caret-color: var(--color-primary-blue);
  font: inherit;
  resize: none;
  padding: 0.5rem 1rem;
  height: 100%;
  width: 100%;
  border: 1px solid var(--color-neutral-gray);
  border-radius: 0.5rem;
  line-height: 1.5;
}

.comment-input-content:focus {
  outline: none;
  border: 1px solid var(--color-primary-blue);
}

.comment-submit-btn {
  font: inherit;
  text-transform: uppercase;
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  color: var(--color-neutral-white);
  background-color: var(--color-primary-blue);
  height: 2.5rem;
}
.comment-submit-btn:hover {
  opacity: 0.5;
}

@media screen and (max-width: 600px) {
  .comment-input-block {
    flex-direction: column;
    align-items: flex-end;
  }
}

.new-comment-block {
  position: relative;
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
}

@media screen and (max-width: 600px) {
  .new-comment-block > .avatar {
    display: inline-block;
    position: absolute;
    bottom: 1rem;
    left: 1rem;
  }
}

.comment-edits {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}
.edit-btn {
  font: inherit;
  background: none;
  border: none;
  text-transform: capitalize;
}
.edit-btn > img {
  margin-right: 0.5rem;
}
.edit-btn:hover {
  cursor: pointer;
  opacity: 0.7;
}
.delete {
  color: var(--color-primary-red);
}
.edit {
  color: var(--color-primary-blue);
}

@media screen and (max-width: 600px) {
  .comment-edits {
    position: absolute;
    bottom: 1.25rem;
    right: 1rem;
  }
}

.comment-block {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-gap: 1rem;
  gap: 1rem;
  width: 100%;
}

.comment {
  position: relative;
  background-color: var(--color-neutral-white);
  padding: 1rem;
  border-radius: 1rem;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  width: 100%;
}

.comment-content {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  width: 100%;
}

/* Comment header */
.comment-content_head {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user-meta {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.reply-svg {
  display: inline-block;
  height: 1rem;
  width: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0.5rem;
}
.reply {
  display: flex;
  align-items: center;
  color: var(--color-primary-blue);
  text-transform: capitalize;
  font-weight: 500;
}
.reply:hover {
  cursor: pointer;
  opacity: 0.5;
  outline: none;
}

.reply-block {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}
.replies {
  width: 92%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-gap: 1rem;
  gap: 1rem;
}
.reply-line-tab {
  width: 8%;
  display: flex;
  justify-content: center;
}
.reply-line {
  width: 2px;
  background-color: var(--color-neutral-blue-gray);
  opacity: 0.5;
}

@media screen and (max-width: 600px) {
  .comment {
    flex-direction: column;
    align-items: flex-start;
  }

  .reply {
    position: absolute;
    bottom: 1.25rem;
    right: 1rem;
  }

  .reply-line-tab {
    width: 0.25rem;
  }
  .replies {
    width: 96%;
  }
}

.comments {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 0.8rem;
  width: 100vw;
  position: fixed;
  bottom: 0;
  color: var(--color-neutral-white);
  padding: 0.25rem;
  background-color: var(--color-neutral-blue-dark);
}

.attributions a {
  text-decoration: none;
  color: var(--color-primary-red-pale);
}
.attributions a:hover {
  cursor: pointer;
  color: var(--color-primary-red);
}

